export interface Scenario {
  id: string;
  title: string;
  description: string;
  iconType: string;
  color: string;
}

export interface Collection {
  id: string;
  title: string;
  description?: string;
  count: number;
  creatorId?: string;
  creatorName?: string;
  items?: string[];
}

export interface Policy {
  id: string;
  title: string;
  description?: string;
  type: 'random' | 'weighted' | 'sorting' | 'exclusion' | 'custom';
  creatorId?: string;
  creatorName?: string;
}

export interface PopularApplication {
  id: string;
  title: string;
  description?: string;
  creator: string;
  likes: number;
  category?: string;
}

